import { NoResultsIcon } from "@rp/react/common/assets"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, CircularProgress, Typography } from "@rp/react/web/mui/core"
import { BOTTOM_NAV_HEIGHT } from "../NavBars"

const FindNoResults = ({
  text = "No results found for your search",
  isLoading = false,
  offset = 0,
  headerHeight = 55,
}) => {
  const { isMobile } = useDeviceInfo()
  return (
    <Box
      sx={{
        height: isMobile
          ? `calc(100dvh - ${headerHeight + BOTTOM_NAV_HEIGHT + 150 + offset}px)`
          : `calc(100vh - ${headerHeight + 30}px)`,
        width: "100%",
        p: 2,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        textAlign: "center",
      }}
    >
      {isLoading ? (
        <CircularProgress />
      ) : (
        <>
          <NoResultsIcon width={200} height={200} />
          <Typography variant="h6" sx={{ pt: 6, fontWeight: 500 }}>
            {text}
          </Typography>
          <Typography sx={{ color: "grey.300" }}>
            We couldn't find any result for your search. Try something different.
          </Typography>
        </>
      )}
    </Box>
  )
}

export { FindNoResults }
